import React, { useState } from 'react';
import MetricCard from '../components/MetricCard'; // মেট্রিক্স কার্ড কম্পোনেন্ট
import DateRangePicker from '../components/DateRangePicker'; // তারিখ রেঞ্জ পিকার
import SalesChart from '../components/SalesChart'; // নতুন সেলস চার্ট কম্পোনেন্ট
import { useApi } from '../hooks/useApi'; // আমাদের কাস্টম API হুক
import { DollarSign, ShoppingBasket, TrendingUp, Percent, BarChart } from 'lucide-react'; // আইকন

// তারিখকে 'YYYY-MM-DD' ফরম্যাটে রূপান্তর করার জন্য একটি হেলপার ফাংশন
const formatDate = (date) => {
    if (!date) return '';
    return date.toISOString().split('T')[0];
};

export default function Overview() {
    // তারিখের স্টেট ম্যানেজমেন্ট: ডিফল্টভাবে গত ৩০ দিনের ডেটা দেখাবে
    const [startDate, setStartDate] = useState(new Date(new Date().setDate(new Date().getDate() - 29)));
    const [endDate, setEndDate] = useState(new Date());

    // ডাইনামিক API এন্ডপয়েন্ট তৈরি, যা তারিখ পরিবর্তনের সাথে সাথে আপডেট হবে
    const overviewEndpoint = `overview?start_date=${formatDate(startDate)}&end_date=${formatDate(endDate)}`;
    
    // API থেকে ওভারভিউ মেট্রিক্সের ডেটা আনা
    const { data: overviewData, loading: loadingMetrics, error: metricsError } = useApi(overviewEndpoint);

    // সংখ্যা ফরম্যাট করার জন্য হেলপার ফাংশন
    const formatCurrency = (value) => `৳${parseFloat(value || 0).toLocaleString('en-IN', { minimumFractionDigits: 2, maximumFractionDigits: 2 })}`;
    const formatNumber = (value) => (value || 0).toLocaleString('en-IN');

    return (
        <div>
            {/* উপরের সেকশন: শিরোনাম এবং তারিখ ফিল্টার */}
            <div className="flex flex-col sm:flex-row justify-between sm:items-center gap-4 mb-6">
                <h2 className="text-xl font-semibold text-gray-700">Key Metrics Overview</h2>
                <DateRangePicker 
                    startDate={startDate} 
                    setStartDate={setStartDate} 
                    endDate={endDate} 
                    setEndDate={setEndDate} 
                />
            </div>

            {/* মেট্রিক্স কার্ড সেকশন */}
            {loadingMetrics && (
                <div className="text-center p-4">
                    <p>Loading Metrics...</p>
                </div>
            )}
            {metricsError && (
                <div className="p-4 bg-red-100 text-red-700 rounded-lg">
                    <p>Error loading metrics: {metricsError}</p>
                </div>
            )}
            
            {overviewData && (
                <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-5">
                    <MetricCard title="Gross Sales" value={formatCurrency(overviewData.gross_sales)} icon={DollarSign} />
                    <MetricCard title="Net Sales" value={formatCurrency(overviewData.net_sales)} icon={DollarSign} />
                    <MetricCard title="Total Orders" value={formatNumber(overviewData.orders_count)} icon={ShoppingBasket} />
                    <MetricCard title="Items Sold" value={formatNumber(overviewData.items_sold)} icon={ShoppingBasket} />
                    <MetricCard title="Avg. Order Value" value={formatCurrency(overviewData.avg_order_value)} icon={TrendingUp} />
                </div>
            )}

            {/* চার্ট সেকশন */}
            <div className="mt-8 bg-white p-4 sm:p-6 rounded-xl shadow">
                <h2 className="text-xl font-semibold text-gray-800 mb-4">Sales Over Time</h2>
                <div className="h-96">
                    {/* ডাইনামিক চার্ট কম্পোনেন্ট, যা তারিখের স্টেট পাস করে */}
                    <SalesChart startDate={startDate} endDate={endDate} />
                </div>
            </div>
        </div>
    );
}